.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    margin-top: 10px;
    margin-left: 550px;
    width: 50vw;
    
    /* width: 100%; */
  }
  
  td,th {
    border: 1px solid #232058;
    text-align: left;
    padding: 2px;
    margin-top: 10px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
  
  
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
  input:checked+.slider {
    background-color: #2196f3;
  }
  
  input:focus+.slider {
    box-shadow: 0 0 1px #2196f3;
  }
  
  
  
  
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }